.container {
  padding-top: 3rem;
  padding-bottom: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f8f9fa;
}

.news {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 1.7rem;
  margin-bottom: 2rem;
}
@media screen and (max-width: 1000px) {
  .container {
    width: 1000px;
  }
}
