.pl {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 50px 100px;
}
.plText {
  width: 65%;
}
.plTitle {
  font-size: 50px;
  font-weight: 600;
}
.plDesc {
  margin: 20px 0px;
}
.plList {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media screen and (max-width: 1000px) {
  .plList {
    justify-content: center;
  }
}
